
<template>
  <div display="flex">
    <tool-bar></tool-bar>
    <div id="content">
      <!-- <router-link v-bind:to="`/delivery/post/${delivery.post_id}`" class="link">  </router-link>  -->
      <div id="sort-box"> 
        <!--    -->
        <!-- <button> </button>
        <button>:   </button> -->
        <label> </label>
          <div>
            <select class="sortThing" v-model="category" v-on:focus="NoneCategory" v-on:focusout="SelectCategory">
              <option value="" selected="selected" disabled hidden>-----  -----</option>
              <!--      data.categories    -->
              <option value="KOR"></option>
              <option value="CHI"></option>
              <option value="ETC"></option> 
            </select>  
          </div>

        <label> </label>
          <div>
            <select class="sortThing" v-model="sort_criteria" v-on:focus="BeforeSort" v-on:focusout="SelectSortCriteria">
              <option value="" selected="selected" disabled hidden>-----  -----</option>
              <option value="default">   </option>
              <option value="near">  </option>
              <option value="far">  </option>
            </select>
          </div>

        <div>
          <button type="button" class="btn2" v-on:click="searchArea"> </button>
        </div>

        <div class="add-btn">
          <router-link to="/delivery/post/create" class="link">
            <i class="fa-solid fa-circle-plus"></i>
          </router-link>
        </div>
      </div>

      <!------------------------------------    ---------------------------------------->
      <div v-for="delivery in this.$store.state.deliveryPostList" class="listbox" v-bind:key="delivery">
        <img class="listbox-img" :src="require('../assets/' + delivery.category + '.png')" alt="">
        <div class="listbox-content">
          <div class="listbox-head">
            <div class="res-name"><router-link v-bind:to="`/delivery/post/${delivery.post_id}`">{{ delivery.post_name }}</router-link></div>
            <div class="category" v-bind:class="delivery.category">{{this.categories[delivery.category]}}</div>
            <div class="time"><small> : {{  delivery.valid_time.format("M/D  HH:mm")}}</small></div>
          </div>
          <router-link v-bind:to="`/delivery/post/${delivery.post_id}`"><p>{{ delivery.post_content }}</p></router-link>
          <div class="listbox-foot">
            <div class="detail-info">
              <small>{{delivery.area_name}}</small>
              <button class="area-btn"><img src="../assets/placeholder.png">{{ delivery.take_loc }}</button>
              <img src="../assets/people.png" alt="">
              <small class="cnt">{{ delivery.participant_num }}/{{ delivery.max_person_num }}</small>
            </div>
            <div class="ago">
              <small>{{caltime(delivery.createdDate)}} </small>
            </div>
          </div>
        </div>
      </div>
      <!------------------------------------------------------------------------------------------------->  
    </div>
    <up-button id="up_button"></up-button>
    <!--    relative      -->
    <footer-area id="footer"></footer-area>
  </div>
</template>

<script>
import ToolBar from '../components/ToolBar.vue'
import FooterArea from '../components/FooterArea.vue'
import UpButton from '../components/UpButton.vue'
import dayjs from 'dayjs'

export default {
  components:{
    ToolBar,
    FooterArea,
    UpButton,
  },
  data(){
    return {
      categories:{
        'KOR': '',
        'CHI': '',
        'JAP': '',
        'ETC': ''
      },
      category: "",
      sort_criteria: "",
      area: "",
    } 
  },
  beforeCreate(){
    this.$store.dispatch('FETCH_DELIVERY_LIST');
  },
  created() {
    this.$store.dispatch('FIND_POST_ID', this.$store.state.member.nick);
  },
  methods:{
    caltime(created){
      const now = dayjs();
      if(created.isSame(now,"day")){
          const ago_H = now.diff(created,"h");
          const ago_M = now.diff(created,"m");
          if(ago_H == 0){
            return String(ago_M)+" ";
          }
          return String(ago_H)+" "+String(ago_M)+" ";
         }
      else{
          const ago_D = now.diff(created,"d");
          return String(ago_D)+" ";
      }
    },
    NoneCategory(){
      this.category = "";
    },
    SelectCategory(){
      // this.area = this.$store.getters.GET_DELIVERY_PRESENT_AREA;
      // if(this.$store.state.delivery_category_area == "" && this.$store.state.delivery_category_sort == ""){

      // }
      // else{
      //   this.$store.dispatch('FETCH_DELIVERY_LIST_CATEGORY', this.category);
      //   console.log(this.category);
      // }
      this.$store.dispatch('FETCH_DELIVERY_LIST_CATEGORY', this.category);
      console.log(this.category);
      // console.log(this.area);
      setTimeout(() => { 
          this.$store.getters.GET_DELIVERIES;
        }, 200);   
    },
    BeforeSort(){
      this.sort_criteria = "";
    },
    SelectSortCriteria(){
      if(this.sort_criteria == "near" | this.sort_criteria == 'far'){
        this.$store.dispatch('FETCH_DELIVERY_LIST_SORT_TIME', this.sort_criteria);
      }
      else if(this.sort_criteria == "default"){
        this.$store.dispatch('FETCH_DELIVERY_LIST')
      }
      //     if  
      setTimeout(() => { 
        this.$store.getters.GET_DELIVERIES;
      }, 100);   
    },
    searchArea() {
      new window.daum.Postcode({
        oncomplete: (data) => {
          console.log("API:  ",data);
          const sido = data.sido;
          const sigoongu = data.sigungu;
          const eupmyeondong = data.bname;
          
          this.area = sido+" "+sigoongu+" "+eupmyeondong;
          this.$store.dispatch('FETCH_DELIVERY_LIST_SORT_AREA', this.area);
          console.log(this.area);

          setTimeout(() => { 
            this.$store.getters.GET_DELIVERIES;
          }, 200);  
        }
      }).open();
    }
  }
}
</script>

<style scoped>

#wrap{
    height : 100%;
    position: relative;
}
#content{
    margin: 0 auto;
    width: 700px;
    padding: 100px 0;
      
}
#footer{
    height: 300px;
    position: absolute;  
    width: 100%;
    left: 0;
}

#up_button{
    position: fixed;
    bottom: 70px;
    right: 200px;
    z-index: 1000;
}
#sort-box{
  display: -webkit-box;
  margin: 10px 12px 10px 23px;
}

#sort-box button{
  font-size: 10px;
  margin: 0px 5px;
  padding: 5px 8px;
  background-color: #767676;
  border: 1px solid #767676;
  color: white;
  box-sizing: border-box;
  border-radius: 20px;
}
.add-btn{
  -webkit-box-flex:1;
  text-align: right;
}
.fa-solid{
  font-size: 25px;
  color: #767676;
}
.listbox, .listbox-head, .listbox-foot{
  display: -webkit-box;
}

.listbox{
  padding: 20px 0;
  border-bottom: 1px solid #eeeeef;
}
.listbox-img{
  width: 100px;
  margin: 0 30px;
  border-radius: 10px;
}
.listbox-content{
  -webkit-box-flex:1;
  padding-right: 15px;
}

.listbox-content p{
  text-align: left;
  font-size: 13px;
  line-height: 20px;
  color: #666;
}
a {
  text-decoration: none;
}
.listbox-head{
  line-height: 20px;
}
.res-name a{
  font-weight: bold;
  color: #000;
  text-decoration: none;
}
.category{
  font-size: xx-small;
  margin: 0px 7px;
  padding: 0 7px;
  height: 19px;
  border-radius: 3px;
  color: #ffffff;
  vertical-align:middle;
}
.sortThing {
  width:45%;
  
}
label {
  text-align: left;
  display: block;
  font-size: 15px;
  font-weight: bold;
  color: #292929;
  margin: 0 0 12px;
}

/* -----     -------*/
.KOR{
  background-color: #6157cf;
}
.CHI{
  background-color: #ff6e6c;
}
.JAP{
  background-color: #ff0400;
}
.ETC{
  background-color: #8c8c8c;
}
/* ---------------------------------- */

.detail-info{
  line-height: 30px;
}
.detail-info img{
  vertical-align:middle;
  width: 20px;
}

button img{
  margin: 0;
}
.cnt{
  font-family: 'Roboto';
  line-height: 20px;
  padding-left: 5px;
}
.area-btn{
  margin: 0px 15px;
  height: 30px;
  padding: 0px 14px 0px 10px;
  box-sizing: border-box;
  border-radius: 27px;
  border: 1px solid #afafaf;
  background-color: white;
  color: #5d5d5d;
}
.time{
  -webkit-box-flex:1;
  text-align: right;
  line-height: 20px;
  color:#ff6b33;
}

.ago{
  -webkit-box-flex:1;
  text-align: right;
  line-height: 30px;
  color: #959595;
}
.time small, .ago small{
  vertical-align:middle;
  font-size: 11px;
}

button {
  font-family: 'Noto Sans KR', sans-serif;
  width: 100%;
  height: 40px;
  background: #ffffff;
  color: #7aab85;
  border: 1px solid #7aab85;
  box-sizing: border-box;
  border-radius: 13px;
}

.btn1, .btn2 {
  padding: 8px 15px 9px;
}


</style>