<template>
  <div>
    <tool-bar></tool-bar>
    <div id="wrap">
      <input v-model="notice_title" id="notice_title" placeholder=" .">
      <hr>
      <textarea v-model="notice_content" id="notice_content" type="text" placeholder="  "></textarea>
      <button type="submit" v-on:click.prevent="authorization"></button>
    </div>
  </div>
</template>

<script>
import ToolBar from '../components/ToolBar.vue'
import Swal from 'sweetalert2';
import dayjs from "dayjs";

export default {
  components:{
      ToolBar,
  },
  data() {
    return {
      notice_title: "",     //  
      notice_content: "",  //  
    }
  },
  methods: {
    authorization(){
      if (this.submitData()){
        // this.$store.dispatch('FETCH_USER')//   api  
        setTimeout(() => { 
          // if(  )
            this.register();
        }, 100);   
      }
      else {
        alert("  .")
      }
    },
    register() {
        const noticeDTO = {
          title: this.notice_title,           //  
          // author: this.$store.state.member.nick,
          author: " ",
          post_time: dayjs(),
          content: this.notice_content,     //  
          view_num: 0,
        }
        console.log("NOTICEDTO\n",noticeDTO);
        this.$store.dispatch('POST_NOTICE', this.noticeDTO)
        //              
				Swal.fire({
          icon: 'success',
          title: ' .',
          confirmButtonText: ' ',
        }).then(() => {
          this.$router.replace("/notice/post");
        })
			},
      submitData(){
        if(this.notice_title != "" && this.notice_content != ""){
          return true;
        }
        else{
          return false;
        }
      },
    },
}
</script>

<style scoped>
#notice_title{
  font-weight: bold;
  font-size: 25px;
  color: #555;
  border: none;
  outline: none;
  margin-top: 5px;
  padding-left: 5px;
}
#notice_content{
  font-size: 15px;
  height: 400px;
  width: 300px;
  padding: 5px;
  border: none;
  outline: none;
}
#wrap {
  background-color: #f2f2f2;
  height: 800px;
  padding: 80px 0;
}
</style>